<template>
  <div style="width: 100%;height: 100%;">
    <div id="root"
         class="d3-body">
      <img id="node-scroll-button"
           :class="[isShow ? '':'reached']"
           src="../../public/static/img/arrow.svg">
    </div>
  </div>
</template>
<style scoped>
#root {
  overflow-x: auto;
  scrollbar-width: none;
  scroll-behavior: smooth;
  margin-bottom: -17px;
  position: relative;
  /* padding: 100px 0 0; */
}
.reached {
  z-index: -1;
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-30px);
  }
  60% {
    transform: translateX(-15px);
  }
}
#node-scroll-button {
  position: absolute;
  right: 20px;
  padding: 10px;
  margin-top: 300px;
  animation: bounce 2s infinite;
  cursor: pointer;
  transition: opacity 0.3s ease-in;
}
</style>
<script>
export default {
  data() {
    return {
      isShow: true
    }
  },
  mounted() {
    let _this = this
    console.log(_this.isShow)
    //横向滚动
    $('#root').scroll(function() {
      let root = document.getElementById('root')
      document.getElementById('node-scroll-button').style.right =
        20 - root.scrollLeft + 'px'
      // console.log(root.scrollLeft, root.offsetWidth, root.clientWidth)
      let scrollled = Math.ceil(root.scrollLeft + root.offsetWidth)
      console.log(scrollled)
      if (scrollled < 1920) {
        _this.isShow = true
      } else {
        _this.isShow = false
      }
    })
    //创建svg
    var svg = d3
      .select('#root')
      .append('svg')
      .attr('width', 1920)
      .attr('height', 800)
      .style('background-color', 'rgb(0,0,0)')
    // var margin = [100, 100, 100, 100]
    //构建颜色比例尺
    // var color = d3.scaleOrdinal(d3.schemeCategory10)
    // var color = 'rgba(113,86,153,0.4)'
    //模拟数据:
    var nodes_data = [
      //color                 main:'rgba(113,86,153,0.4)'   less:'rgba(30,130,45,0.4)'  normal:'rgba(115,166,233,0.4)' strong:'rgba(188,86,158,0.4)'
      { name: 'Client', r: 50, color: 'rgba(113,86,153,0.4)' }, //0
      { name: 'Server', r: 50, color: 'rgba(113,86,153,0.4)' }, //1
      //client-------------1--------------
      { name: 'HTML', r: 42, color: 'rgba(188,86,158,0.4)' }, //2 0-1
      { name: 'JavaScript', r: 42, color: 'rgba(188,86,158,0.4)' }, //3 0-2
      { name: 'CSS', r: 42, color: 'rgba(188,86,158,0.4)' }, //4 0-3
      { name: 'XML', r: 42, color: 'rgba(115,166,233,0.4)' }, //5 0-4
      { name: 'SVG', r: 42, color: 'rgba(188,86,158,0.4)' }, //6 0-5
      //-------------------2--------------
      { name: 'LESS', r: 36, color: 'rgba(30,130,45,0.4)' }, //7 4-1
      { name: 'SCSS', r: 36, color: 'rgba(188,86,158,0.4)' }, //8 4-2
      { name: 'jQuery', r: 36, color: 'rgba(188,86,158,0.4)' }, //9 3-1
      { name: 'Vue.js', r: 36, color: 'rgba(188,86,158,0.4)' }, //10 3-2
      { name: 'Webpack', r: 36, color: 'rgba(188,86,158,0.4)' }, //11 3-3
      { name: '小程序', r: 36, color: 'rgba(188,86,158,0.4)' }, //12 3-4
      { name: 'D3.js', r: 36, color: 'rgba(30,130,45,0.4)' }, //13 3-5
      { name: 'Electron', r: 36, color: 'rgba(30,130,45,0.4)' }, //14 3-6
      { name: 'NodeJS', r: 36, color: 'rgba(188,86,158,0.4)' }, //15 3-7
      { name: 'JSON', r: 36, color: 'rgba(188,86,158,0.4)' }, //16 3-8
      { name: 'Ajax', r: 36, color: 'rgba(188,86,158,0.4)' }, //17 3-9
      { name: 'ES6/7', r: 36, color: 'rgba(188,86,158,0.4)' }, //18 3-10
      //----------------------3----------------------------
      { name: 'Babel', r: 30, color: 'rgba(30,130,45,0.4)' }, //19 11-1
      { name: 'mpvue', r: 30, color: 'rgba(115,166,233,0.4' }, //20 12-1
      { name: 'Axios', r: 30, color: 'rgba(188,86,158,0.4)' }, //21 17-1
      //--------------server-----1----
      { name: 'Java', r: 36, color: 'rgba(115,166,233,0.4)' }, //22 1-1
      { name: 'MySQL', r: 36, color: 'rgba(115,166,233,0.4)' }, //23 1-2
      { name: 'Git', r: 36, color: 'rgba(188,86,158,0.4)' }, //24 1-3
      //----------------------------2------------------------------
      { name: 'SpringMVC', r: 30, color: 'rgba(30,130,45,0.4)' } //25 22-1
    ]
    var edges_data = [
      //-----------1------------
      { source: 1, target: 0 },
      { source: 2, target: 0 },
      { source: 3, target: 0 },
      { source: 4, target: 0 },
      { source: 5, target: 0 },
      { source: 6, target: 0 },
      //---------2--------------
      { source: 7, target: 4 },
      { source: 8, target: 4 },
      { source: 9, target: 3 },
      { source: 10, target: 3 },
      { source: 11, target: 3 },
      { source: 12, target: 3 },
      { source: 13, target: 3 },
      { source: 14, target: 3 },
      { source: 15, target: 1 },
      { source: 16, target: 3 },
      { source: 17, target: 3 },
      { source: 18, target: 3 },
      //-----------------3-------------
      { source: 19, target: 11 },
      { source: 20, target: 12 },
      { source: 21, target: 17 },
      //--------server-------1---------
      { source: 22, target: 1 },
      { source: 23, target: 1 },
      { source: 24, target: 1 },
      //----------------2--------------
      { source: 25, target: 22 }
    ]
    var simulation = d3
      .forceSimulation(nodes_data)
      .force('charge', d3.forceManyBody().strength(-1200))
      .force(
        'link',
        d3
          .forceLink(edges_data)
          .distance(45)
          .strength(1)
      )
      .force('center', d3.forceCenter(750, 375))
      .on('tick', tick)
    //绘制links
    var link = svg
      .selectAll('g.link')
      .data(edges_data)
      .enter()
      .append('g')
      .attr('class', 'link')
      .append('line')
      .attr('stroke', 'rgba(255,255,255,0.2)')
      .attr('stroke-width', 1)
      .attr('x1', function(d) {
        return d.source.x
      })
      .attr('y1', function(d) {
        return d.source.y
      })
      .attr('x2', function(d) {
        return d.target.x
      })
      .attr('y2', function(d) {
        return d.target.y
      })
    var dragged = d3
      .drag()
      .container(document.getElementsByTagName('svg')[0])
      .on('start', function(d) {
        if (!d3.event.active) simulation.alphaTarget(0.6).restart()
        d.fx = d.x
        d.fy = d.y
      }) //mousedown
      .on('drag', function(d) {
        d.fx = d3.event.x
        d.fy = d3.event.y
      }) //mousemove
      .on('end', function(d) {
        console.log(d3.event.subject)
        if (!d3.event.active) simulation.alphaTarget(0)
        d.fx = null
        d.fy = null
      }) //mousedown

    //绘制节点
    var nodes = svg
      .selectAll('g.nodes')
      .data(nodes_data)
      .enter()
      .append('g')
      .attr('class', 'nodes')
      .call(dragged)
    var node = nodes
      .append('circle')
      .attr('cx', function(d) {
        console.log('d:', d)
        return d.x
      })
      .attr('cy', function(d) {
        return d.y
      })
      // .attr('r', 20)
      .attr('r', function(d) {
        return d.r
      })
      .attr('fill', function(d, i) {
        // return color(i)
        return d.color
      })
    var nodeT = nodes
      .append('text')
      .attr('stroke', '#DCDCDC')
      .attr('stroke-width', 1)
      .attr('x', function(d) {
        return d.x
      })
      .attr('y', function(d) {
        return d.y
      })
      .text(function(d) {
        return d.name
      })
    //当力模拟完成后修改显示状态
    function tick() {
      link
        .attr('x1', function(d) {
          return d.source.x
        })
        .attr('y1', function(d) {
          return d.source.y
        })
        .attr('x2', function(d) {
          return d.target.x
        })
        .attr('y2', function(d) {
          return d.target.y
        })
      node
        .attr('cx', function(d) {
          return d.x
        })
        .attr('cy', function(d) {
          return d.y
        })
      nodeT
        .attr('x', function(d) {
          var textL = d.name.length
          return d.x - (textL / 2) * 5.9
        })
        .attr('y', function(d) {
          return d.y + 5
        })
    }
  }
}
</script>